.fab-container {
  position: absolute;
  bottom: 5%;
  right: 5%;
  display: flex;
  align-items: center;
  article {
    margin-left: 1rem;
    background-color: white;
    border-radius: 20px;
  }
}

.fab-main {
  width: 10rem;
  height: 3.5rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  h3,
  h4 {
    font-weight: unset;
    cursor: pointer;
  }
  h3:hover,
  h3:focus {
    color: var(--primary-color);
  }
  h4:hover,
  h4:focus {
    color: #f85959;
  }
}

.fab-create-note {
  // width: 10rem;
  height: 2rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fab-image-container {
  cursor: pointer;
  height: 100%;
  margin-left: 1rem;
  img {
    width: 100%;
    height: 100%;
  }
}

.fab-proceed-button {
  padding: 0.25em 2em;
  border-radius: 10px;
  border-color: var(--primary-color);
  cursor: pointer;
  border-style: solid;
  background-color: var(--primary-color);
  color: white;
}
