.ctm-body {
  background-color: #fff;
  width: 90%;
  max-width: 400px;
  height: 50%;
  border-radius: 50px;
  padding: 2rem;
}

.ctm-content-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.ctm-header-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.ctm-image-container {
  width: 4.5rem;
  img {
    width: 100%;
    height: 100%;
  }
}

.ctm-flares-container {
  margin: 0 auto;
  width: 70%;
  ul {
    list-style: none;
    li > span {
      margin: 0 0.5rem;
    }
    input[type='checkbox'] {
      -webkit-appearance: none;
      background-color: #fff;
      border: 1px solid var(--primary-color);
      padding: 6px;
      border-radius: 50%;
    }
    input[type='checkbox']:checked {
      background-color: var(--primary-color);
    }
  }
}

.ctm-buttons-container {
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
  width: 90%;
  button {
    width: 40%;
    height: 2rem;
    border-radius: 10px;
    border-color: var(--primary-color);
    cursor: pointer;
    border-style: solid;
  }
  & > button:first-child {
    background-color: var(--primary-color);
    color: white;
  }
  & > button:last-child {
    background-color: white;
    color: var(--primary-color);
  }
}
