.side-nav {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 0;
  ul {
    list-style: none;
    li {
      margin-bottom: 0.2rem;
    }
  }
}

.nav-info-logo-container__user {
  display: flex;
  align-items: flex-start;
  margin-top: 5rem;
}

.nav-info-logo-container {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  transition: 0s background-color;
  border-radius: 10px;
  &:hover {
    background-color: hsl(0, 0%, 90%);
    transition-delay: 0.1s;
  }
}

.nav-logo-container {
  width: 1.5rem;
  margin-left: 1rem;
  img {
    width: 100%;
  }
}

.nav-text-container {
  h3 {
    font-size: 1.5rem;
  }
  h6,
  h3 {
    font-weight: unset;
  }
}

.nav-links {
  text-decoration: none;
}
